import {Component, OnInit} from '@angular/core';
import {SignalViewModel} from 'src/app/core/models/customer/signal/signal-viewmodel';
import {ActivatedRoute} from '@angular/router';
import { SignalService } from 'src/app/core/services/customer/notification/hubs/signal.service';

@Component({
	selector: 'app-chat',
	templateUrl: './chat.component.html',
	styleUrls: ['./chat.component.less']
})
export class ChatComponent implements OnInit {
	signalList: SignalViewModel[] = [];
	userId: string;
	constructor(private signalService: SignalService, private route: ActivatedRoute) {}
	ngOnInit(): void {
		this.signalService.signalReceived.subscribe((signal: SignalViewModel) => {
			this.signalList.push(signal);
			console.log(signal);
		});
	}
	// getQuery() {
	// 	this.route.queryParamMap.subscribe((result) => {
	// 		this.userId = result['userId'];
	// 	});
	// }
}
